
import {payOrder} from "@/api/pay"
import Msg from 'vux/src/components/msg/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
props: {

},
data() {
return {
icon:"info",
title:"如没有疑问请确认支付",
isw:true
};
},
computed: {

},
created() {

},
watch: {

},
methods: {
payOrder:function(){
var sing= this.$route.query.sing;
var dataset=this;
payOrder(sing).then(function(result){
if(result==1){
dataset.icon="success";
dataset.title="支付成功";
dataset.isw=false;
}else{
dataset.icon="warn";
dataset.title="支付失败";
dataset.isw=true;
}
}).catch(key=>{
dataset.icon="warn";
dataset.title="支付失败";
dataset.isw=true;
});
}
},
components: {
Msg, Divider, XButton
},
};



